// + openPopup
const openPopup = () => {};

// + btnOpenPopup
const btnOpenPopup = () => {
  $('.btn-open-popup').on('click', (e) => {
    let target = $(e.currentTarget),
      selectedTarget = target.attr('data-target');
    // popup show
    $('.c-layer-popup' + selectedTarget)
      .addClass('show')
      .attr('aria-hidden', false);
  });
};

// + btnClosePopup
const btnClosePopup = () => {
  $('.c-modal-header .popup-cls-btn, .c-modal-footer .popup-cls-btn').on('click', (e) => {
    const target = $(e.currentTarget);
    // popup hide
    target.parents('.c-layer-popup').attr('aria-hidden', true).removeClass('show');
  });
};

